import React from 'react';
import { NavLink } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import LocaleSwitch from '../LocaleSwitch/LocaleSwitch';

interface Props {
  sidebarOpen: boolean,
  setSidebarOpen: React.Dispatch<React.SetStateAction<boolean>>
}

function Sidebar({ sidebarOpen, setSidebarOpen }: Props) {
  const { t } = useTranslation();

  const handleOverlayClick = (): void => {
    setSidebarOpen(false);
  };

  const handleSidebarClick = (e: React.MouseEvent): void => {
    e.stopPropagation();
  };

  return (
    <div
      role="banner"
      className={`sidebar-container ${sidebarOpen ? 'sidebar-container-open' : ''}`}
      onClick={handleOverlayClick}
    >
      <ul className="sidebar" onClick={(e) => handleSidebarClick(e)}>
        <li><NavLink to="/">{t('nav.home')}</NavLink></li>
        <li><NavLink to="/login">{t('nav.login')}</NavLink></li>
        <li><NavLink to="/register">{t('nav.register')}</NavLink></li>
        <li><NavLink to="/about">{t('nav.about')}</NavLink></li>
        <li><NavLink to="/upload">{t('nav.upload_measurement')}</NavLink></li>
        <li><NavLink to="/measurement">{t('nav.measurement')}</NavLink></li>
        <li><NavLink to="/user">{t('nav.user')}</NavLink></li>
        <LocaleSwitch />
      </ul>
    </div>
  );
}

export default Sidebar;
