import React from 'react';
import { NavLink } from 'react-router-dom';
import Table from 'react-bootstrap/Table';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ImageWithLabel from '../Layout/ImageWithLabel/ImageWithLabel';

function Measurement() {
  return (
    <>
      <h1>Pozorování - CzeV371</h1>
      <section>
        Autor:
        {' '}
        <NavLink to="/user" className="text-white">Dominik Juriga</NavLink>
      </section>
      <section>
        <Row>
          <Col lg={6}>
            <ImageWithLabel imageUrl="/data_files/curvegenerator.gif" altText="movement of star" labelText="Křivka" />
          </Col>
          <Col lg={6}>
            <ImageWithLabel imageUrl="/data_files/resize_image.jfif" altText="map of stars" labelText="Mapa" />
          </Col>
        </Row>
      </section>
      <section>
        <Table responsive striped bordered variant="dark">
          <tbody>
            <tr>
              <td className="w-50">Formát JD</td>
              <td>geocentrické</td>
            </tr>
            <tr>
              <td>Datum</td>
              <td>9.11.2021 20:51</td>
            </tr>
            <tr>
              <td>Fotometrický filtr</td>
              <td>Clear</td>
            </tr>
            <tr>
              <td>Fotometrický systém</td>
              <td>instrumentální </td>
            </tr>
          </tbody>
        </Table>
      </section>
    </>
  );
}

export default Measurement;
