import React from 'react';
import { useTranslation } from 'react-i18next';
import UploadMeasurement from '../Forms/UploadMeasurement';

function Upload() {
  const { t } = useTranslation();

  return (
    <>
      <h1>{t('pages.upload.header')}</h1>
      <UploadMeasurement />
    </>
  );
}

export default Upload;
