/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';
import { useTranslation } from 'react-i18next';

type Inputs = {
  username: string,
  password: string
}

function Login() {
  const { t } = useTranslation();
  const { register, handleSubmit, formState: { errors } } = useForm<Inputs>({
    defaultValues: {
      username: '',
      password: '',
    },
  });

  const onSubmit: SubmitHandler<Inputs> = (data) => {
    console.log(data);
  };

  return (
    <>
      <h1>{t('pages.login.header')}</h1>

      <form onSubmit={handleSubmit(onSubmit)}>

        <label htmlFor="username">
          <span>Username</span>
          {errors.username && <span className="error-message">{errors.username.message}</span>}
          <input id="username" {...register('username', { required: 'This field is required.' })} placeholder="Username" />
        </label>

        <label htmlFor="password">
          <span>Password</span>
          {errors.password && <span className="error-message">{errors.password.message}</span>}
          <input id="password" {...register('password', { required: 'This field is required.' })} placeholder="Password" />
        </label>

        <input type="submit" />
      </form>
    </>
  );
}

export default Login;
