import React, { useState } from 'react';
import { toast } from 'react-toastify';
import FakeValidator from './FakeValidator';

interface Values {
  name: string,
  JD: string,
  filter: string,
}

function UploadMeasurement() {
  const [values, setValues] = useState<Values | null>(null);
  const [map, setMap] = useState<string | null>(null);
  const validateFile = async () => {
    toast.info('Validuji soubor..');
    const res = await FakeValidator(500);
    setValues(res);
    toast.success('Soubor je validní');
  };

  const handleFileChange = (fl: FileList | null): void => {
    if (fl !== null) {
      validateFile();
    }
  };

  const handleMapChange = (fl: FileList | null): void => {
    if (fl !== null) {
      setMap(URL.createObjectURL(fl[0]));
    }
  };

  return (
    <form>
      <label htmlFor="name">
        Název hvězdy
        <input type="text" value={values?.name} />
      </label>

      <label htmlFor="file">
        Datový soubor
        <input type="file" accept=".txt" onChange={(e) => handleFileChange(e.target.files)} />
      </label>

      <label htmlFor="map">
        Mapa
        <input type="file" accept=".jpg,.png,.gif,.jfif" onChange={(e) => handleMapChange(e.target.files)} />
        {map && <img src={map} alt="map" />}
      </label>

      <label htmlFor="JD">
        Formát JD
        <select value={values?.JD}>
          <option value="" disabled>Zvolit</option>
          <option value="geocentric">Geocentric</option>
          <option value="heliocentric">Heliocentric</option>
        </select>
      </label>

      <label htmlFor="filter">
        Filter
        <select value={values?.filter}>
          <option value="" disabled>Zvolit</option>
          <option value="U">U</option>
          <option value="B">B</option>
          <option value="clear">Clear</option>
        </select>
      </label>

      <input type="submit" value="Uložit" />
    </form>
  );
}

export default UploadMeasurement;
